import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListChemistComponent } from './list-chemist.component';
import { ListChemistRoutingModule } from './list-chemist-routing.module';
import { SharedModule } from '../../../shared/shared.module';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import '../../../../assets/jquery-locationpicker-plugin-master/src/locationpicker.jquery.js';

@NgModule({
  imports: [
    CommonModule,
    ListChemistRoutingModule,
    SharedModule,
    NgxDatatableModule,
    FormsModule, 
    ReactiveFormsModule,
    HttpModule
  ],
  declarations: [ListChemistComponent]
})
export class ListChemistModule { }



