import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';

import { GetPopularChemistService } from '../../services/GetPopularChemistService';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { AddAreaService } from '../../services/AddAreaService';
import { AddchemistService } from '../../services/AddChemistService';

import { ValidationModel } from './../../models/validation.class';

import swal from 'sweetalert2';

declare var jQuery: any;

@Component({
  selector: 'list-chemist',
  templateUrl: './list-chemist.component.html',
  styleUrls: [
    './list-chemist.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [GetPopularChemistService, GetChemistDataService, AddAreaService, AddchemistService]
})
export class ListChemistComponent implements OnInit {
  rowsBasic = [];
  fullScreenRow = [];
  loadingIndicator = true;
  reorderable = true;

  columns = [
    { prop: 'PharmacyName' },
    { prop: 'Address' },
    { prop: 'City.Name', name: 'City' },
    { prop: 'PharmaCommission', name: 'Commission' },
    { prop: 'RatingCount', name: 'Rating' },
    { name: "Actions", sortable: "false", prop: "id" }
  ];

  rows = [];
  expanded = {};
  timeout: any;

  rowsFilter = [];
  tempFilter = [];

  /**
   * Pharma Variables
   */
  chemists_array = [];
  cities_array = [];
  areas_array = [];
  public FullName: string = "";
  public Address: string = "";
  public Email: string = "";
  public PharmaCommission: number = 0;
  public City: number = 0;
  public Area: number = 0;
  public PharmacistId: number = 0;
  public Username: string = "";
  public Password: string = "";
  public Latitude: number = 0;
  public Longitude: number = 0;


  public runner: boolean = false;
  /**
   * Pharma Variables End
   */
  @ViewChild(DatatableComponent) table: DatatableComponent;

  constructor(public _getPopularChemistService: GetPopularChemistService,
    public _getChemistDataService: GetChemistDataService,
    public _AddAreaNameService: AddAreaService,
    public _AddChemistService: AddchemistService) {
    this.getChemists();
    this.getAllCities();
  }

  onSelect({ selected }) {
    console.log(selected);
  }

  onActivate(event) { }

  backToList(){
    jQuery('#detail').hide();
    jQuery('#list').show();
  }

  getChemists() {
    this._getChemistDataService.getRegisteredChemistService().subscribe(
      response => {
        for (var i = 0; i < response.data.length; i++) {
          this.chemists_array.push(response.data[i]);
        }
        console.log(this.chemists_array);
        this.tempFilter = this.chemists_array;
        this.rowsFilter = this.chemists_array;
      });
  }// End of Get Chemists

  getAllCities() {
    this._getChemistDataService.getAllCitiesService().subscribe(
      response => {
        this.cities_array = response.data;
      });
  }// End of Get All Cities

  getCityAreas() {
    this._AddAreaNameService.getAreaName(this.City).subscribe(
      response => {
        this.areas_array = response.data;
      });
  }// End of Get Areas of City

  updateChemist() {
    if (this.runner == false) {
      $('#loader').show();
      $('.text-danger').hide();
      this.runner = true;
      if (ValidationModel.validateString(this.FullName)) {
        this.runner = false;
        $('#loader').hide();
        $('#full_name_id_error').show();
        return;
      }
      if (ValidationModel.validateString(this.Address)) {
        this.runner = false;
        $('#loader').hide();
        $('#address_id_error').show();
        return;
      }
      if (ValidationModel.validateNumber(this.PharmaCommission)) {
        this.runner = false;
        $('#loader').hide();
        $('#pharma_commission_id_error').show();
        return;
      }
      if (ValidationModel.validateNumber(this.City)) {
        this.runner = false;
        $('#loader').hide();
        $('#city_id_error').show();
        return;
      }
      if (ValidationModel.validateNumber(this.Area)) {
        this.runner = false;
        $('#loader').hide();
        $('#area_id_error').show();
        return;
      }
      if (ValidationModel.validateNumber(this.Latitude)) {
        this.runner = false;
        $('#loader').hide();
        $('#latitude_id_error').show();
        return;
      }
      if (ValidationModel.validateNumber(this.Longitude)) {
        this.runner = false;
        $('#loader').hide();
        $('#longitude_id_error').show();
        return;
      }
    }
    this._AddChemistService.updateChemist(this.Area, this.PharmaCommission, this.City, this.FullName, this.Longitude, this.Latitude, this.PharmacistId, this.Address).subscribe(response => {
      $('#loader').hide();
      this.runner = false;
      console.log(response.data);
      $('#update-message').show();
      setTimeout(function(){
        $('#update-message').hide();
      }, 5000);
    });
  }

  view(Id) {
/*     jQuery('#detail').show();
    jQuery('#list').hide();
    var pharmacist_obj = this.chemists_array.find(x => x.Id == Id);
    console.log(pharmacist_obj);
    this.FullName = pharmacist_obj.PharmacyName;
    this.Address = pharmacist_obj.Address;
    this.Email = pharmacist_obj.Email;
    this.PharmaCommission = pharmacist_obj.PharmaCommission;
    this.City = pharmacist_obj.CityId;
    this.getCityAreas();
    var self = this;

    this.Area = pharmacist_obj.AreaId;
    this.PharmacistId = pharmacist_obj.Id;
    this.Username = pharmacist_obj.Username;
    this.Password = pharmacist_obj.Password;
    this.Latitude = pharmacist_obj.Latitude;
    this.Longitude = pharmacist_obj.Longitude;
    jQuery('#somecomponent').locationpicker({
      location: {
        latitude: self.Latitude,
        longitude: self.Longitude
      },
      radius: 300,
      inputBinding: {
        latitudeInput: self.Latitude,
        longitudeInput: self.Latitude,
        radiusInput: jQuery('#us7-radius'),
        locationNameInput: jQuery('#us7-address'),
      },
      enableAutocomplete: true,
      resize: true,
      autosize: true
    }); */
  }

  //Delete Services
  blockUnBlock(i) {
    console.log(i);
    var self = this;
    swal({
      title: 'Are you sure?',
      text: 'You wont be able to revert',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, Update it!'
    }).then(function (result) {
      if (result.value == true) {
        self._AddChemistService.blockUnBlockChemist(i).subscribe(response => {
          console.log(response);
          swal(
            'Deleted!',
            'Pharmacy Status has been Updated.',
            'success'
          );
          this.getChemists();
        });
      }
      else {
      }
    }).catch(swal.noop);
  }

  ngOnInit(): void {
    var self = this;
    setTimeout(function () {
      jQuery('#somecomponent').locationpicker({
        location: {
          latitude: 30.3753,
          longitude: 69.3451
        },
        radius: 300,
        inputBinding: {
          latitudeInput: self.Latitude,
          longitudeInput: self.Longitude,
          radiusInput: jQuery('#us7-radius'),
          locationNameInput: jQuery('#us7-address'),
        },
        onchanged: function (currentLocation, radius, isMarkerDropped) {
          console.log(currentLocation);  //latlon  
          self.Latitude = currentLocation.latitude;
          self.Longitude = currentLocation.longitude;
        },
        enableAutocomplete: true,
        resize: true,
        autosize: true
      });
    }, 3000);
  }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();
    console.log(val);
    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.PharmacyName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

  onPage(event) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      console.log('paged!', event);
    }, 100);
  }
  getRowHeight(row) {
    return row.height;
  }

}



