import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ListChemistComponent } from './list-chemist.component';

const routes: Routes = [
  {
    path: '',
    component: ListChemistComponent,
    data: {
      title: 'Ecommerce',
      status: false
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ListChemistRoutingModule { }



